<template>
    <div>
      <el-row :gutter="15" class="top-layout">
        <el-col :span="8"><div class="grid-content bg-purple-dark month-plan-sta hidden">
          <p class="order-title">托管客户统计</p>
          <div id="tourCustomerSta"></div>
        </div></el-col>
        <el-col :span="16"><div class="grid-content bg-purple-dark month-plan-sta hidden">
          <p class="order-title left">托管客户列表</p>
          <p class="right tour-search">
            <el-input placeholder="请输入内容" v-model="inputCustomer" size="small">
              <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
          </p>
          <el-table
            :data="tableCustomerData"
            stripe
            height="289"
            style="width: 100%">
            <el-table-column
              prop="customerName"
              label="客户名称"
              width="">
            </el-table-column>
            <el-table-column
              prop="customerType"
              label="客户类型"
              width="">
            </el-table-column>
            <el-table-column
              prop="contact"
              label="联系人">
            </el-table-column>
            <el-table-column
              prop="contactPhone"
              label="联系电话">
            </el-table-column>
            <el-table-column
              prop="customerAddress"
              label="客户地址"
              show-overflow-tooltip>
            </el-table-column>
          </el-table>
        </div></el-col>
      </el-row>
      <el-row :gutter="15" class="top-layout">
        <el-col :span="8"><div class="grid-content bg-purple-dark month-plan-sta hidden">
          <p class="order-title">托管项目统计</p>
          <div id="tourProjectSta"></div>
        </div></el-col>
        <el-col :span="16"><div class="grid-content bg-purple-dark month-plan-sta hidden">
          <p class="order-title left">托管项目列表</p>
          <p class="right tour-search">
            <el-input placeholder="请输入内容" v-model="inputProject" size="small">
              <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
          </p>
          <el-table
            :data="tableProjectData"
            stripe
            height="289"
            style="width: 100%">
            <el-table-column
              prop="projectName"
              label="项目名称"
              width="">
            </el-table-column>
            <el-table-column
              prop="projectAddress"
              label="项目地址"
              show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              prop="contact"
              label="联系人">
            </el-table-column>
            <el-table-column
              prop="view"
              label="查看"
              width="">
              <template slot-scope="scope">
                <el-button @click="dialogPropertyVisible = true" type="text" size="small" class="view-btn">资产清单</el-button>
                <el-button @click="dialogProtocolVisible = true" type="text" size="small" class="view-btn">协议合同</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div></el-col>
      </el-row>
      <el-dialog title="托管资产清单" :visible.sync="dialogPropertyVisible">
        <el-table :data="gridData">
          <el-table-column property="date" label="日期" width="150"></el-table-column>
          <el-table-column property="name" label="姓名" width="200"></el-table-column>
          <el-table-column property="address" label="地址"></el-table-column>
        </el-table>
      </el-dialog>
      <el-dialog title="2018年6月横琴新区光伏电站项目资产清单" :visible.sync="dialogProtocolVisible">
        <el-table :data="gridData">
          <el-table-column property="date" label="日期" width="150"></el-table-column>
          <el-table-column property="name" label="姓名" width="200"></el-table-column>
          <el-table-column property="address" label="地址"></el-table-column>
        </el-table>
      </el-dialog>
    </div>
</template>

<script>
  import echarts from "echarts";
    export default {
        data () {
            return {
              inputCustomer:'',
              inputProject:'',
              tableCustomerData: [{
                customerName: '国际海洋度假区',
                customerType: '客户类型',
                contact: '赵钱孙',
                contactPhone:'133-0099-1122',
                customerAddress:'珠海市横琴新区长隆国际度假'
              }, {
                customerName: '国际海洋度假区',
                customerType: '客户类型',
                contact: '赵钱孙',
                contactPhone:'133-0099-1122',
                customerAddress:'珠海市横琴新区长隆国际度假'
              },{
                customerName: '国际海洋度假区',
                customerType: '客户类型',
                contact: '赵钱孙',
                contactPhone:'133-0099-1122',
                customerAddress:'珠海市横琴新区长隆国际度假'
              },{
                customerName: '国际海洋度假区',
                customerType: '客户类型',
                contact: '赵钱孙',
                contactPhone:'133-0099-1122',
                customerAddress:'珠海市横琴新区长隆国际度假'
              },{
                customerName: '国际海洋度假区',
                customerType: '客户类型',
                contact: '赵钱孙',
                contactPhone:'133-0099-1122',
                customerAddress:'珠海市横琴新区长隆国际度假'
              },{
                customerName: '国际海洋度假区',
                customerType: '客户类型',
                contact: '赵钱孙',
                contactPhone:'133-0099-1122',
                customerAddress:'珠海市横琴新区长隆国际度假'
              }],
              tableProjectData:[{
                projectName: '国际海洋度假区',
                projectAddress: '客户类型',
                contact: '赵钱孙',
              },{
                projectName: '国际海洋度假区',
                projectAddress: '客户类型',
                contact: '赵钱孙',
              },{
                projectName: '国际海洋度假区',
                projectAddress: '客户类型',
                contact: '赵钱孙',
              },{
                projectName: '国际海洋度假区',
                projectAddress: '客户类型',
                contact: '赵钱孙',
              },{
                projectName: '国际海洋度假区',
                projectAddress: '客户类型',
                contact: '赵钱孙',
              },{
                projectName: '国际海洋度假区',
                projectAddress: '客户类型',
                contact: '赵钱孙',
              },{
                projectName: '国际海洋度假区',
                projectAddress: '客户类型',
                contact: '赵钱孙',
              }],
              gridData: [{
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
              },{
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
              },{
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
              },{
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
              },{
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
              },{
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
              }],
              dialogPropertyVisible: false,
              dialogProtocolVisible:false
            }
        },
      mounted(){
        var that = this;
        //that.gettourCustomerSta();
        //that.gettourProjectSta();
        that.getCustomerSta();
        that.getProjectSta();
        that.getCustomerList();
        that.getProjectList();

      },
      methods:{
        getCustomerSta(){
          let that = this;
          that.$http
            .get(this.BASE_INFO.BASE_URL+"api/resource/getCustomerSta", {})
            .then(function(response) {
              if(response && response.data && response.data.data){
                var  data=response.data;
                that.getCustomerStaData(data.data);
              }
            });
        },
        getCustomerStaData(data){
          if(data){
            let that = this;
            var xData = [];
            var barData = [];
            for(var i=0;i<data.length;i++){
              xData.push(data[i].time);
              barData.push(data[i].value);
            }
            that.gettourCustomerSta(xData,barData);
          }
        },
        getProjectSta(){
          let that = this;
          that.$http
            .get(this.BASE_INFO.BASE_URL+"api/resource/getProjectSta", {})
            .then(function(response) {
              if(response && response.data && response.data.data){
                var  data=response.data;
                that.getProjectStaData(data.data);
              }
            });
        },
        getProjectStaData(data){
          if(data){
            let that = this;
            var xData = [];
            var barData = [];
            for(var i=0;i<data.length;i++){
              xData.push(data[i].time);
              barData.push(data[i].value);
            }
            that.gettourProjectSta(xData,barData);
          }
        },
        gettourCustomerSta(xData,barData){
          var that = this;
          // 基于准备好的dom，初始化echarts实例
          var myChart = echarts.init(document.getElementById("tourCustomerSta"));
          // 指定图表的配置项和数据
          var option = {
            color: ['#3398DB'],
            tooltip : {
              trigger: 'axis',
              axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
              }
            },
            grid: {
              x: "6%",
              y: "8%",
              x2: "1%",
              y2: "8%"
            },
            xAxis : [
              {
                type : 'category',
                data : xData,
                axisLabel:{
                  color:'#666666'
                },
                axisLine:{
                  lineStyle:{
                    color:'#e6e8ed'
                  }
                }
              }
            ],
            yAxis : [
              {
                type : 'value',
                nameTextStyle: {
                  //Y轴坐标名称颜色
                  color: "#666666"
                },
                //Y轴刻度线
                axisTick:{
                  show:false
                },
                //Y轴数字
                axisLabel:{
                  color:'#666666'
                },
                //横向坐标网格线
                splitLine:{
                  lineStyle:{
                    color:'#e6e8ed'
                  }
                },
                //X轴坐标线
                axisLine:{
                  lineStyle:{
                    color:'#e6e8ed'
                  }
                }
              }
            ],
            series : [
              {
                name:'托管客户',
                type:'bar',
                barWidth: '60%',
                data:barData
              }
            ]
          };
          // 使用刚指定的配置项和数据显示图表。
          myChart.setOption(option);
        },
        gettourProjectSta(xData,barData){
          var that = this;
          // 基于准备好的dom，初始化echarts实例
          var myChart = echarts.init(document.getElementById("tourProjectSta"));
          // 指定图表的配置项和数据
          var option = {
            color: ['#3398DB'],
            tooltip : {
              trigger: 'axis',
              axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
              }
            },
            grid: {
              x: "6%",
              y: "8%",
              x2: "1%",
              y2: "8%"
            },
            xAxis : [
              {
                type : 'category',
                data : xData,
                axisLabel:{
                  color:'#666666'
                },
                axisLine:{
                  lineStyle:{
                    color:'#e6e8ed'
                  }
                }
              }
            ],
            yAxis : [
              {
                type : 'value',
                nameTextStyle: {
                  //Y轴坐标名称颜色
                  color: "#666666"
                },
                //Y轴刻度线
                axisTick:{
                  show:false
                },
                //Y轴数字
                axisLabel:{
                  color:'#666666'
                },
                //横向坐标网格线
                splitLine:{
                  lineStyle:{
                    color:'#e6e8ed'
                  }
                },
                //X轴坐标线
                axisLine:{
                  lineStyle:{
                    color:'#e6e8ed'
                  }
                }
              }
            ],
            series : [
              {
                name:'托管项目',
                type:'bar',
                barWidth: '60%',
                data:barData
              }
            ]
          };
          // 使用刚指定的配置项和数据显示图表。
          myChart.setOption(option);
        },
        getCustomerList(){
          let that = this;
          that.$http
            .get(this.BASE_INFO.BASE_URL+"api/resource/getCustomerList", {})
            .then(function(response) {
              if(response && response.data && response.data.data){
                var  data=response.data.data;
                that.tableCustomerData = data.data;
                //that.getProjectStaData(data.data);
              }
            });
        },
        getProjectList(){
          let that = this;
          that.$http
            .get(this.BASE_INFO.BASE_URL+"api/resource/getProjectList", {})
            .then(function(response) {
              if(response && response.data && response.data.data){
                var  data=response.data.data;
                that.tableProjectData = data.data;
                //that.getProjectStaData(data.data);
              }
            });
        },
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .tour-search .el-input-group__append{
    padding: 0 5px;
  }
  .view-btn{
    padding: 9px 0px;
    margin-right: 15px;
  }
</style>



